@import url(https://fonts.googleapis.com/css?family=Poiret+One);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
:root{color-scheme:light dark;--primary: #0a2202;--primary-light: #62f085;--primary-dark: #0a2202;--background: #62f085;--secondary: #fbff17;--secondary-dark: #76e0d2;--tertiary: #7e0311;--tertiary-dark: #b84a5a;--tertiary-light: #ffc3cc;--stroke: #121629}@media(prefers-color-scheme: light){:root{--primary: #0a2202;--background: #62f085}}@media(prefers-color-scheme: dark){:root{--primary: #62f085;--background: #0a2202}}html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}#root{height:100%;position:absolute;left:0;width:100%;overflow:hidden}body{margin:0;padding:0;line-height:1;font-family:"Roboto",sans-serif;color:#0a2202;color:var(--primary);background-color:#62f085;background-color:var(--background);font-smooth:always;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}ul{margin:0;padding:0}.container{width:100%;height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;color:#0a2202;color:var(--primary);background-color:#62f085;background-color:var(--background)}.input{height:3em;display:flex;width:200px;justify-content:flex-end;align-items:center;font-weight:bold;font-size:1.4em;background-color:#000;color:#fff;padding:.5em;outline:1px solid #888;border-radius:10%}.input.evaluate{color:#62f085;color:var(--primary-light)}.btn{background:#7e0311;border-radius:4px;font-family:"Montserrat",cursive;font-size:1.4em;font-weight:bolder;justify-content:center;align-items:center;text-align:center;border:.0625rem solid #121629;border:.0625rem solid var(--stroke);display:flex;flex:1 1;outline:1px solid #121629;outline:1px solid var(--stroke);cursor:pointer}.btn:hover{background:#56020c;transition:all .3s ease}.btn:active{background:#010000}.btn#clear{grid-area:clear}.btn#zero{grid-area:zero}.btn#equals{grid-area:equals}.btn#nine{grid-area:nine}.btn#divide{grid-area:divide}.btn#eight{grid-area:eight}.btn#seven{grid-area:seven}.btn#six{grid-area:six}.btn#five{grid-area:five}.btn#four{grid-area:four}.btn#three{grid-area:three}.btn#decimal{grid-area:decimal}.row{display:grid;margin-top:10px;grid-template-columns:repeat(4, 50px);grid-template-rows:repeat(5, 2rem);grid-template-areas:"clear clear divide multiply" "seven eight nine substract" "four five six add" "one two three equals" "zero zero decimal equals"}.calculator{padding:10px;width:250px;height:380px;border:.3rem solid #76e0d2;border:.3rem solid var(--secondary-dark);border-radius:10%;display:flex;flex-wrap:wrap;justify-content:center;align-items:center;text-align:center;flex-basis:content}
